

import Icon from '../UI/Icon';

import classes from './contact.module.css';

import Twitter from '../../img/twitter.svg';
import LinkedIn from '../../img/in.svg';
import GitHub from '../../img/github.svg';
import Call from '../../img/call.svg';
import Input from '../UI/Input';
import {  useState } from 'react';

const Contact =({rectRef,...rest}) => {

    const [submitting,setSubmitting] = useState(false);
    const [status,setStatus] = useState(undefined);
    const [error,setError] = useState(false);
    const submitHandler = (e) => {
        e.preventDefault();
        
        setSubmitting(true);
        var data = new FormData(e.target);
        fetch(e.target.action, {
          method: e.target.method,
          body: data,
          headers: {
              'Accept': 'application/json'
          },
          mode:'no-cors',
        }).then(response => {
            setStatus("Thanks for your submission!");
            setError(false);
            e.target.reset()
          }).catch(error => {
              setSubmitting(false);
              setError(true);
              setStatus("Oops! There was a problem submitting your form");
          });
      
    }

    return(
        <section id='contact' className={classes.Contact} {...rest} ref={rectRef} >
            <h3>Contact</h3>
            <div className={classes.Grid}>
                <div className={classes.Online}>
                    <p>You can reach me on the following platform:</p>
                    <div className={classes.Grid}>
                    <Icon imgSrc={LinkedIn} title='LinkedIn Icon' 
                        page='https://www.linkedin.com/in/uzezi-oghenekaro-js/'
                        target='_blank' />
                    <Icon imgSrc={GitHub} title='Github icon' 
                        page='https://github.com/jephterSmart/'
                        target='_blank' />
                    <Icon imgSrc={Twitter} title='twitter Icon' 
                        page='https://twitter.com/jephterSmart/'
                            target='_blank' />
                    </div>
                    
                    or
                    <p>You can call<span className={classes.Call}>
                            <Icon imgSrc={Call} title='Call Icon' page="tel:+2348076884964"/>
                        </span>  me 
                        
                   </p>
                </div>
                <div className={classes.Form}>
                    <form onSubmit={submitHandler} action='https://formspree.io/f/xoqyldgv' method="POST">
                        <Input label='Name:' id='name' type='text'  name="name" />
                        <Input label='Email:' id='email'  type='email'  name="email"/>
                        <Input label='Message:' id='message' textArea rows={8} name="message" />
                        <button type='submit' disabled={submitting}>Send</button>
                        {status && (<p className={`${error ? classes.Error: classes.Good}`}> {status}</p>)}
                    </form>
                </div>
            </div>
        </section>
    )
}
export default Contact;