
import Card from '../UI/Card';

import BgImg1 from '../../img/works/burger1.jpg';
import BgImg2 from '../../img/works/burger2.jpg';
import BgImg3 from '../../img/works/burger3.jpg';
import BgImg4 from '../../img/works/burger4.jpg';
import BgImg5 from '../../img/works/burger5.jpg';
import BgImg6 from '../../img/works/burger6.jpg';

import jephterImg1 from '../../img/works/shop1.jpg'
import jephterImg2 from '../../img/works/shop2.jpg'
import jephterImg3 from '../../img/works/shop3.jpg'

import hrImg1 from '../../img/works/hr1.jpg';
import hrImg2 from '../../img/works/hr2.jpg';
import hrImg3 from '../../img/works/hr3.jpg';

import messageImg1 from '../../img/works/message1.jpg'
import messageImg2 from '../../img/works/message2.jpg'
import messageImg3 from '../../img/works/message3.jpg'

import classes from './works.module.css';
import useImage from '../../hooks/useImage';


const Work = ({rectRef,...rest}) => {
    const [burgerImg] = useImage(BgImg1,BgImg2,BgImg3,BgImg4,BgImg5,BgImg6);
    const [shopImg] = useImage(jephterImg1,jephterImg2,jephterImg3);
    const [messageImg] = useImage(messageImg1,messageImg2,messageImg3);
    const [hrImg] = useImage(hrImg1,hrImg2,hrImg3);
    
    return (
        <section id='works' className={classes.Work} ref={rectRef} {...rest}  >
            <h3>Works</h3>
            <div>
                <Card imgSrc ={shopImg} title='Buy and Sell Shop' 
                link='https://uzezijephtershop.herokuapp.com' github='https://github.com/jephterSmart/uzezijephtershop'
                text={`This is a online web application where an individual can buy and sell goods.  A visitor can come
                and just buy with an account of visitor. If he has a Seller account, then he can buy and sell.`} />
                <Card imgSrc ={burgerImg} title='Burger App' dark
                link='https://burger-app-6ebce.web.app/' github='https://github.com/jephterSmart/jephter-burger'
                text={`This web application allows you to create a burger of your choosing and orders the created burger.
                So, go get your tasty burger.`} />
                <Card imgSrc ={hrImg} title='Human Resource Manager' 
                link='https://uzezijephter-employee-manager.herokuapp.com/' 
                github='https://github.com/jephterSmart/employeeManagerWithMUI'
                text={`The web application that allows  Human resource manager to create employees with ease, 
                thus helping his/her organisation managing all employees, giving them the necessary access right.`} />
                <Card imgSrc ={messageImg} title='Activity App'  dark
                link='https://uzezijephter-restapi-frontend.herokuapp.com/' 
                github='https://github.com/jephterSmart/Posting_App'
                text={`The web application that allows individuals and user to share their activity, i.e., a user
                that created a post or activity has right to edit or delete it. But every user on the platform or app 
                can view every other person activity and his/her own.`} />
            </div>
        </section>
    )
}
export default Work;