
import classes from './card.module.css';

const Card = ({title,text,imgSrc,className,dark, ...rest}) => {
    //for manipulating classNames
    const cardClasses = [classes.Card];
    if(className) cardClasses.push(className);

    const contentClasses =[classes.Content];
    if(dark) contentClasses.push(classes.Dark)
    else contentClasses.push(classes.Light);

    const centerClasses = [classes.Center];
    if(dark) centerClasses.push(classes.Light);
    else centerClasses.push(classes.Dark);


    return(
        <div className={cardClasses.join(' ') } {...rest}  >
            <div className={contentClasses.join(' ') } >
                <div className={classes.Animate} >
                    <h4 >{title}</h4>
                    <div></div>
                </div>
                <div className={classes.Text}>
                    <div className={classes.Shape} ><div></div><div className={dark?classes.dark:classes.Light}></div></div>
                    <p >{text}  <span>
                    You can see the github page at <a href={rest.github}>{rest.github}</a>
                    </span></p>
                </div>
            </div>
            <div className={centerClasses.join('  ') }>
                <a href={rest.link} target='_blank'>
                View Live<br />
                Application
                </a>
                <span className={`${classes.Line} ${dark?classes.Dark: classes.Light}`} ></span>
            </div>
            <div className={classes.Img} style={{backgroundImage: `url(${imgSrc})`}}>
               
            </div>
        </div>
    )
}
export default Card;