import Navigation from "./Navigation";
import IntroPage from "./IntroPage";
import SinglePage from "./SinglePage/index";

import classes from './layout.module.css';
import { useCallback, useState } from "react";
import Hidden from "./UI/Hidden";

const Layout = () => {
    const [ended,setEnded] = useState(false);
    const [currentSection,setCurrentSection] = useState('home');
    const animationHandler = () => {
        setEnded(true);
    }
    const callback = useCallback((entries, observer) => {
        entries.forEach(entry => {
           
            if(!entry.isIntersecting) return;
            if(entry.isIntersecting){
               
                setCurrentSection(entry.target.id);
                
            }
        })
      },[])
    return(
        <>
        <header className={classes.Header} onAnimationEnd ={animationHandler}>
            <div className={classes.Logo}><a href='#home' >JS</a></div>
            
                <Navigation active={currentSection} />

        </header>
        <main>
            {ended?<SinglePage callback={callback}/>: <IntroPage />}
            
        </main>
        </>
    )
}

export default Layout;