import {useState,useEffect} from 'react';

const useImage = (...rest) => {
    let arr = rest;
    const [burgerImg,setBurgerImg] = useState(arr[0]);
    useEffect(() => {
        const id = setInterval(() => {
          let num =  Math.floor(Math.random() * arr.length);
          setBurgerImg(arr[num]);
        },20000);
        return () => {
            clearInterval(id);
        }
    },[burgerImg,arr])
    return [burgerImg];
}
export default useImage;