import Skill from '../UI/Skill';

import classes from './home.module.css';
import bgImg1 from '../../img/home1.jpg';
import bgImg2 from '../../img/home2.jpg';
import skImg1 from '../../img/nodejs-icon.svg';
import skImg2 from '../../img/docker.svg';
import skImg3 from '../../img/aws.svg';
import skImg4 from '../../img/php.svg';

import skImg5 from '../../img/database-server.svg';
import skImg6 from '../../img/jenkins.svg';



const HomeSection = ({rectRef,...rest}) => {

    return(
        <>
        <section id='home' className={classes.Section}  
        style={{backgroundImage:`url(${Math.floor(Math.random() * 2)=== 0? bgImg1:bgImg2})`}}
        ref={rectRef} {...rest}>
        
        <h2>Full stack web developer</h2>
        <p>I Design &amp; Develop Application that <br />
            make your organisation <span>stand out from the crowd</span>
        </p>
    </section>
    <section className={classes.Skill}>
        <h3>Skills</h3>
        <div className={classes.Grid}>
            <div>
                <Skill img={skImg1} title='JavaScript' content='Nodejs, Express(MVC), React, Webpack' />
                <Skill img={skImg2} title='DevOps' content='Docker, Kubernetes, Ansible, Terraform' />
                <Skill img={skImg3} title='Cloud Engineering' 
                content='AWS EC2, EKS, ACM, ECR, Cloudformation,networking, etc. ' />
                
            </div>
            <div>
                <Skill img={skImg4} title='PHP' content='Laravel' />
                <Skill img={skImg5} title='Databases' content='Oracle, MongoDB, MySQL, DynamoDB, Reddis' />
                <Skill img={skImg6} title='Continuous Integration' 
                content='Jenkins CI,  AWS CodePipeline, AWS CodeBuild' />
            </div>
        </div>
    </section>
    </>
    )
}
export default HomeSection;