
import classes from './about.module.css';

import Img from '../../img/uzezijephter.jpg';

const AboutSection = ({rectRef,...rest}) => {

    return(
        <section id='about' className={classes.Section}{...rest} ref={rectRef}>
            <h3>About</h3>
            <div className={classes.Grid}>
                <div className={classes.Letter}>
                    <p>I am from  a small town, Oleh, Delta state in Nigeria. 
                    I have a Bachelor of Electrical/Electronics Engineering degree from 
                    the University of Benin (UniBen), Benin City. I develop passion for programming from 
                    my early days in the university.
                    </p>
                    <p> I have worked alone and with a team of people for the past three years.
                    During this three years  
                    I have acquired project and time management skills, as well as the ability to communicate with 
                    team members and clients while effectively meeting milestones and deadlines.</p>
                    <p>
                    I have worked on a multitude of web based projects for a range of clients providing Web Design
                    (Photoshop, Figma, AdobeXD) and Development (PHP, Nodejs (Express), HTML, CSS, LESS, jQuery,React), eCommerce Solutions, and Graphic Design. 
                    During my Electrical/Electronics Engineering degree I studied System Architecture, and 
                    therefore have a keen understanding of how every component of the System
                    connect together, which I put to good use on every project I work on.
                    </p>
                    <p>I am currently working with Loctech Nigeria Ltd. I am always available to discuss your project over 
                the phone or via Email.
                These days I enjoy spending my free time baking, playing keyboard and reading.</p>
                </div>
                <div className={classes.Profile} style={{backgroundImage:`url(${Img})`}}>
                </div>
            </div>
        </section>
    )
}
export default AboutSection;